.listWrapper {
    margin-top: 30px;
    padding-bottom: 110px;
}
.list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: solid 1px var(--line-color05);
}
.list:last-child {
    border-bottom: none;
}
.link {
    width: 100%;
    display: block;
    font-size: 26px;
    font-weight: 500;
    line-height: 95px;
    padding: 0 40px;
    font-family: "Noto Sans KR", "sans-serif";
    white-space: nowrap;
    letter-spacing: -1px;
    cursor: pointer;
}